<template>
  <div class="pageContent">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="导航名称：">
            <el-input style="width: 203px" v-model="searchList.name" clearable placeholder="导航名称"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container" style="margin-top:10px" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
        <div style="padding-right:20px;">
          <el-button type="text" style="margin-right:20px;" @click="navShowFun()">系统导航设置</el-button>
          <el-button type="text" icon="el-icon-circle-plus-outline" @click="$router.push({path:'/shop/shopNavBar/newNavBar'})">新增导航</el-button>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <div>
      <el-card style="margin-top:20px;" class="filter-container" shadow="never">
        <el-table ref="multipleTable" :data="list" tooltip-effect="dark" style="width: 100%"
          @selection-change="handleSelectionChange">
          <el-table-column type="selection" :selectable='isDisabled' width="55" disabled="true"></el-table-column>
          <el-table-column label="导航名称">
            <template slot-scope="scope">
              <div class="nameBtn flex-row-center">
                <div style="line-height:40px;">{{scope.row.name}}</div>
                <el-button class="editBtn" type="text" icon="el-icon-edit" @click="editNameShowFun(scope.row)"></el-button>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="$router.push({path:'/shop/shopNavBar/editNavBar',query:{id:scope.row.id}})">编辑</el-button>
              <el-button v-if="scope.row.isDefault == 0" type="text" size="small" @click="deleteItem(scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="flex-row-between" style="padding-top:20px;">
          <div class="flex-row-start">
            <div class="flex-row-start">
              <el-checkbox @change="selectAllSureList" v-model="selectAllSure">当前页全选</el-checkbox>
              <div class="checkBoxDiv">
                已选择 <span style="color:#409EFF">{{multipleSelection.length}}</span> 项
              </div>
            </div>
            <div class="flex-row-start" style="margin-left:20px">
              <el-button @click="deleteAll" type="primary">批量删除</el-button>
            </div>
          </div>
          <div>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-sizes="[5,10,15,20]"
              :page-size="searchList.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="listTotal">
            </el-pagination>
          </div>
        </div>
      </el-card>
    </div>
    <!-- 系统导航设置 -->
    <el-dialog title="" :visible.sync="navShow" width="70%" >
      <el-tabs v-model="activeName">
        <el-tab-pane label="系统导航设置" name="0">
          <el-table :data="navigation1" style="width: 100%">
            <el-table-column prop="name" label="名称">
            </el-table-column>
            <el-table-column label="配置">
              <template slot-scope="scope">
                <el-select v-model="scope.row.navigationId" placeholder="请选择">
                  <el-option
                    v-for="item in shopNavList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                  </el-option>
                </el-select>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="功能导航设置" name="1">
          <el-table :data="navigation2" style="width: 100%">
            <el-table-column prop="name" label="名称">
            </el-table-column>
            <el-table-column label="配置">
              <template slot-scope="scope">
                <el-select v-model="scope.row.navigationId" placeholder="请选择">
                  <el-option
                    v-for="item in shopNavList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                  </el-option>
                </el-select>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <div class="flex-row-center" style="margin-top:40px;">
          <el-button style="margin-right:40px" @click="navShow = false">返 回</el-button>
          <el-button type="primary" :loading="configLoading" @click="subConfig">确 认</el-button>
        </div>
      </el-tabs>
    </el-dialog>
    <!-- 修改名称 -->
    <el-dialog title="导航信息修改" :visible.sync="editNameShow" width="30%" >
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="导航名称：" prop="name">
          <el-input @input="change($event)" v-model="ruleForm.newName" maxlength="15" show-word-limit></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="editNameShow = false">取 消</el-button>
          <el-button :loading="editNameLoading" type="primary" @click="submitForm('ruleForm')">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { 
  getShopNavigationInfoList,
  isShopNavigationExist ,
  updateShopNaigationName,
  deleteShopNaigation,
  getShopNavigationConfig,
  getShopNavigationInfoNoPageList,
  insertNavigationConfig
} from '@/api/shop/navBar'
export default {
  data(){
    return{
      configLoading:false,
      editNameLoading:false,
      searchList:{
        name:'',
        pageNum:1,
        pageSize:5
      },
      list:[],
      listTotal:0,
      multipleSelection:[],
      selectAllSure:false,
      editNameShow:false,
      ruleForm:{},          
      rules: {
        name: [
          { required: true, message: '请输入导航名称', trigger: 'blur' },
        ],
      },
      activeName:'0',
      navShow:false,
      navigation1:[],
      navigation2:[],
      shopNavList:[]
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData();
    this.getSelect();
  },
  methods:{
    subConfig(){
      this.configLoading = true;
      let obj = {
        shopId : this.shop.shopId,
        navigationArray:this.navigation1.concat(this.navigation2)
      }
      insertNavigationConfig(obj).then(res=>{
        console.log('配置',res)
        if(res.code == 200){
          this.$message({
            message: '配置成功',
            type: 'success'
          });
          this.navShow = false;
          setTimeout(()=>{
            this.configLoading = false;
          },1000)
        }else{
          this.$message.error(res.message);
          this.configLoading = false;
        }
      })
    },
    navShowFun(){
      this.getNav();
      this.navShow = true;
    },
    getSelect(){
      getShopNavigationInfoNoPageList(this.shop.shopId).then(res=>{
        console.log('权限配置下拉框',res)
        this.shopNavList = res.data
        this.shopNavList.forEach(e => e.id = '' + e.id)
      })
    },
    getNav(){
      getShopNavigationConfig(this.shop.shopId).then(res=>{
        console.log('获取店铺配置',res)
        this.navigation1 = res.data.navigation1
        this.navigation1.forEach(e => {
          e.type = 1
          e.navigationId = '' + e.navigationId
        })
        this.navigation2 = res.data.navigation2
        this.navigation2.forEach(e => {
          e.type = 2
          e.navigationId = '' + e.navigationId
        })
      })
    },
    // 
    deleteItem(e){
      this.$confirm('此操作将删除该数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteFun(e.id)
      }).catch(() => {});
    },
    deleteAll(){
      if(this.multipleSelection.length == 0){
        this.$message.error('请选择数据后再试');
        return;
      }
      this.$confirm('此操作将删除该数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let arr = []
        this.multipleSelection.forEach(e=>arr.push(e.id))
        arr = arr.join(',')
        this.deleteFun(arr)
      }).catch(() => {});
    },
    deleteFun(e){
      deleteShopNaigation({ids:e}).then(res=>{
        console.log('删除',res)
        if(res.code == 200){
          this.$message({
            message: '删除成功',
            type: 'success'
          });
          this.getData();
          this.getSelect()
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 修改名称
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if(this.ruleForm.oldName == this.ruleForm.newName){
            console.log('傻逼没改直接点的确定')
            this.$message({
              message: '修改成功',
              type: 'success'
            });
            return;
          }
          this.editNameLoading = true;
          let obj = {
            shopId:this.shop.shopId,
            name:this.ruleForm.newName,
            id:this.ruleForm.id
          }
          isShopNavigationExist(obj).then(res=>{
            console.log('验证是否重名',res)
            if(res.code == 200){
              updateShopNaigationName(obj).then(res1=>{
                console.log('改名',res1)
                if(res.code == 200){
                  this.$message({
                    message: '修改成功',
                    type: 'success'
                  });
                  this.editNameShow = false;
                  this.getData();
                  setTimeout(()=>{this.editNameLoading = false;},1000)
                }else{
                  this.editNameLoading = false;
                  this.$message.error(res.message);
                }
              })
            }else{
              this.editNameLoading = false;
              this.$message.error(res.message);
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    // 修改名称提示框
    editNameShowFun(e){
      this.ruleForm = e
      this.ruleForm.oldName = e.name;
      this.ruleForm.newName = e.name;
      this.editNameShow = true;
    },
    // 表格 - 默认禁选中
    isDisabled(row,index){
      return !row.isDefault
    },
    getData(){
      getShopNavigationInfoList(this.searchList).then(res=>{
        console.log('获取店铺导航列表',res)
        this.list = res.data.list
        this.listTotal = res.data.total
      })
    },
    reset(){
      this.searchList.name = '';
      this.getData()
    },
    // 底部固定 - 全选
    selectAllSureList(){
      if (this.selectAllSure) {
        this.selectAllSure = false;
        this.$refs.multipleTable.toggleAllSelection();
      } else {
        this.selectAllSure = true;
        this.$refs.multipleTable.clearSelection();
      }
    },
    // 表单选择
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      this.selectAllSure = (this.multipleSelection.length == this.list.length-1) && (this.list.length != 0)
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    change(e){
      this.$forceUpdate()
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .nameBtn{
    width: 100%;
    .editBtn{
      display: none;
      margin-left: 10px;
    }
    &:hover{
      .editBtn{
        display: block;
      }
    }
  }
  .checkBoxDiv{
    font-size: 14px;
    margin-left: 40px;
    line-height: 19px;
    color: #606266;
  }
</style>